import { type ComponentPropsWithRef, forwardRef, useRef } from "react";
import { ErrorTip } from "./ErrorTip";
import { FieldLabel } from "./Label";
import styled from "styled-components";
import {
  textfieldFrameInputStyle,
  type TextfieldFrameInputStyleProps,
} from "./styles";

interface TextFrameSingleLineProps extends ComponentPropsWithRef<"input"> {
  /** @description control input error style & display under input element */
  errorMessage?: string;
  /**
   * @default true
   * @description error tip block default owns 16px height; when set hasError to false, remove error tip block no matter if error message exists.
   */
  hasError?: boolean;
  /**
   * @default false
   * @description will trigger input.select() when true
   */
  autoSelect?: boolean;
  /** @description label text */
  label?: string;
  /** @description override FieldLabel props */
  labelProps?: ComponentPropsWithRef<"label">;
  /** @description override ErrorTip props */
  errorProps?: ComponentPropsWithRef<typeof ErrorTip>;
  /**
   * @default baseColor.Volcano5 `#EE8152`
   * @description override error color for border & icon
   */
  $errorColor?: React.CSSProperties["color"];
}

/** @design https://www.figma.com/design/JGFHYME2p7RpeMYcoMqooS/ODE-Library-1.0?node-id=7870-18042&t=zbZGBrco5ra09mre-4 */
export const TextFrameSingleLine = forwardRef<
  HTMLInputElement,
  TextFrameSingleLineProps
>((props, ref) => {
  const _inputRef = useRef<HTMLInputElement | null>(null);
  const {
    hasError = true,
    autoSelect = false,
    errorMessage,
    label = "",
    labelProps,
    errorProps,
    $errorColor,
    ...inputParams
  } = props;
  /** @description sync width to label & error if exists inside input style */
  const width = props.style?.width;
  const minWidth = props.style?.minWidth;
  const maxWidth = props.style?.maxWidth;
  return (
    <>
      <>
        <FieldLabel
          {...labelProps}
          text={label}
          style={{
            ...labelDefaultStyles,
            width,
            minWidth,
            maxWidth,
            ...labelProps?.style,
          }}
          $required={inputParams.required}
        />
        <br />
      </>
      <Input
        /** @description clear auto complete */
        autoComplete="new-password"
        /** @description fix FF & Safari cannot accept decimal and float numbers */
        step={inputParams.type === "number" ? "any" : inputParams.step}
        {...inputParams}
        style={{ ...inputDefaultStyles, ...inputParams?.style }}
        $error={!!errorMessage}
        $errorColor={$errorColor}
        onFocus={(e) => {
          if (props.onFocus) props.onFocus(e);
          if (autoSelect && _inputRef.current) _inputRef.current.select();
        }}
        ref={(element) => {
          _inputRef.current = element;
          if (!ref) return;
          if (typeof ref === "function") {
            ref(element);
            return;
          }
          ref.current = element;
        }}
      />
      {hasError && (
        <ErrorTip
          {...errorProps}
          style={{ width, minWidth, maxWidth, ...errorProps?.style }}
          message={props.disabled ? undefined : errorMessage}
          $errorColor={$errorColor}
        />
      )}
    </>
  );
});

export const Input = styled.input<TextfieldFrameInputStyleProps>`
  height: 44px;
  ${textfieldFrameInputStyle}
  transition: border-color 0.2s ease, color 0.2s ease;
`;

const labelDefaultStyles = {
  display: "inline-block",
  marginBottom: 4,
} satisfies React.CSSProperties;

const inputDefaultStyles = {
  marginBottom: 4,
} satisfies React.CSSProperties;
